import Tag from 'primevue/tag';

export default {
  components: {
    Tag,
  },

  props: {
    value: {
      type: Number,
      default: 0,
    },
  },

  computed: {
    text() {
      switch (this.value) {
        case 0:
          return '';
        case 1:
          return ' ';
        case 2:
          return '';
        case 3:
          return '';
        default:
          return '';
      }
    },
  },
};
